import React from "react";
import PropTypes from "prop-types";
import { withRouter } from "react-router";
import Header from "./Header";

class Adaptive extends React.Component {
  static propTypes = {
    match: PropTypes.object.isRequired,
    location: PropTypes.object.isRequired,
    history: PropTypes.object.isRequired,
  };

  render() {
    const { match, location, history } = this.props;
    let headerColor;
    if (location.pathname === "/services") {
        headerColor = { background: "linear-gradient(to right, #ff6a00, #ffaa00)" }
    }
    else if (location.pathname === "/about" && "/career" && "/contact") {
        headerColor = { background: "linear-gradient(135deg, #232532, $font-color-dark, #232532)" }
    }
    return (
      <div className="header-container" style={headerColor}>
        <Header />
      </div>
    );
  }
}

const AdaptiveHeader = withRouter(Adaptive);

export default AdaptiveHeader;



